#app .van-nav-bar {
  background: #b83320;
}
.home .navbar .van-nav-bar .van-nav-bar__content {
  height: 50px;
}
.home .navbar .van-nav-bar .van-nav-bar__content .van-nav-bar__title button {
  background: transparent;
  height: 26px;
  border: 1px solid white;
  font-size: 14px;
  color: white;
}
.home .navbar .van-nav-bar .van-nav-bar__content .van-nav-bar__title button:first-child {
  border-radius: 26px 0 0 26px;
}
.home .navbar .van-nav-bar .van-nav-bar__content .van-nav-bar__title button:last-child {
  border-radius: 0 26px 26px 0;
}
.home .navbar .van-nav-bar .van-nav-bar__content .van-nav-bar__title button.active {
  background: white;
  color: #888;
}
.home .navbar .van-nav-bar .van-nav-bar__content .van-nav-bar__right .van-image:first-child {
  margin-right: 10px;
}
.home .navbar .search {
  height: 40px;
  padding: 5px 10px;
  box-shadow: 0 1px 3px 0 #dfdfdf;
}
.home .navbar .search .search-box {
  background: #cfcfcf;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
}
.home .navbar .search .search-box .van-swipe .van-swipe-item {
  line-height: 30px;
}
.home .banner .lately-read {
  color: #888;
  line-height: 40px;
  font-size: 14px;
  margin-bottom: 10px;
  background: white;
}
.home .banner .lately-read span {
  margin: 0 15px;
}
.home .banner .lately-read span:nth-child(2) {
  color: #b83320;
}
