


































































































































































































































































































#app .van-nav-bar {
  background: #b83320;
}
.home {
  .navbar {
    .van-nav-bar {
      .van-nav-bar__content {
        height: 50px;
        .van-nav-bar__title {
          button {
            background: transparent;
            height: 26px;
            // line-height: 26px;
            border: 1px solid white;
            font-size: 14px;
            color: white;
            &:first-child {
              border-radius: 26px 0 0 26px;
            }
            &:last-child {
              border-radius: 0 26px 26px 0;
            }
            &.active {
              background: white;
              color: #888;
            }
          }
        }
        .van-nav-bar__right {
          .van-image {
            &:first-child {
              margin-right: 10px;
            }
          }
        }
      }
    }
    .search {
      height: 40px;
      padding: 5px 10px;
      // border-bottom: 1px solid #dfdfdf;
      box-shadow: 0 1px 3px 0 #dfdfdf;
      .search-box {
        background: #cfcfcf;
        border-radius: 4px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 10px;
        .van-swipe {
          .van-swipe-item {
            line-height: 30px;
          }
        }
      }
    }
  }

  .banner {
    .van-grid {
      // border-bottom: 1px solid lightgray;
    }
    .lately-read {
      color: #888;
      line-height: 40px;
      font-size: 14px;
      margin-bottom: 10px;
      background: white;
      span{
        margin: 0 15px;
      }
      span:nth-child(2){
        color: #b83320;
      }
    }
  }
}
