li.book-item {
  padding-right: 10px;
  display: flex;
}
li.book-item .van-image {
  vertical-align: middle;
}
li.book-item .info {
  flex: 1;
  margin-left: 10px;
  line-height: 20px;
  font-size: 12px;
}
li.book-item .info h5.name {
  line-height: 20px;
  font-size: 14px;
}
li.book-item .info .desc {
  color: #888;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin: 5px 0;
}
li.book-item .info .category {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
li.book-item .info .category span {
  color: #666;
}
li.book-item .info .category span.split {
  margin: 0 5px;
}
li.book-item .info .category span.c-red {
  color: #b83320;
}
li.book-item .info .category i.tag {
  font-size: 10px;
  font-style: normal;
  border: 1px solid #FEC224;
  color: #FEC224;
  line-height: 1.2em;
  padding: 0 3px;
  border-radius: 4px;
}
