











































li.book-item {
  padding-right: 10px;
  display: flex;
  .van-image {
    vertical-align: middle;
  }
  .info {
    flex: 1;
    margin-left: 10px;

    line-height: 20px;
    font-size: 12px;
    h5.name {
      line-height: 20px;
      font-size: 14px;
    }
    .desc {
      color: #888;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;

      margin: 5px 0;
    }
    .category {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        color: #666;
        &.split {
          margin: 0 5px;
        }
        &.c-red {
          color: #b83320;
        }
      }
      i.tag {
        font-size: 10px;
        font-style: normal;
        border: 1px solid #FEC224;
        color: #FEC224;
        line-height: 1.2em;
        padding: 0 3px;
        border-radius: 4px;
      }
    }
  }
}
