






























































































































































































.ranking {
  .van-row {
    .van-col {
      display: flex;
      justify-content: center;
      font-size: 14px;
      span {
        padding: 0 3px;
        line-height: 46px;
      }
      &.active {
        color: red;
        span {
          border-bottom: 2px solid red;
        }
      }
    }
  }
  .list {
    display: flex;
    .van-sidebar {
      height: calc(100vh - 48px);
      overflow-y: auto;
    }
    .right {
      background: white;
      flex: 1;
      height: calc(100vh - 48px);
      overflow-y: auto;
    }
  }
}
