.ranking .van-row .van-col {
  display: flex;
  justify-content: center;
  font-size: 14px;
}
.ranking .van-row .van-col span {
  padding: 0 3px;
  line-height: 46px;
}
.ranking .van-row .van-col.active {
  color: red;
}
.ranking .van-row .van-col.active span {
  border-bottom: 2px solid red;
}
.ranking .list {
  display: flex;
}
.ranking .list .van-sidebar {
  height: calc(100vh - 48px);
  overflow-y: auto;
}
.ranking .list .right {
  background: white;
  flex: 1;
  height: calc(100vh - 48px);
  overflow-y: auto;
}
